body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.border {
  border-radius: 3px;
  border: 1px solid #EEEFFF;
  background-clip: padding-box;
  position: absolute;
  background-color: #EEEFFF;
}
.border:before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
}
.border:hover {
  background-color: #2ECC40;
  border-color: #2ECC40;
  z-index: 1;
  cursor: pointer;
}
.border.active {
  background-color: #666666;
  border-color: #666666;
  z-index: 1;
  cursor: default;
}
